.body {
    width: 100%;
    height: 100vh;
    background-color: #ffac79;
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    margin: 0;
    padding: 0;
}

.title {
    text-align: center;
    font-size: 40px;
    margin-bottom: -40px;
    margin-top: 20vh;
    position: relative;
    color: red;
    z-index: 99999;
}

.circles:after {
    content: '';
    display: inline-block;
    width: 100%;
    height: 100px;
    background: #fff;
    position: absolute;
    top: -50px;
    left: 0;
    bottom: -180px;
    transform: skewY(-4deg);
    -webkit-transform: skewY(-4deg);
}

.circles {
    background: #fff;
    text-align: center;
    position: relative;
    height: 70vh;
    margin-top: -10px;
    box-shadow: inset -1px -4px 4px rgba(0, 0, 0, 0.2);
    justify-content: center;
}
.smalltag {
    margin-top: 200px;
}
.circles .ptag {
    font-size: 240px;
    color: #fff;
    // background-color: #ccc;
    padding-top: 15vh;
    position: relative;
    z-index: 9;
    line-height: 100%;
}

.circles .ptag .smalltag {
    font-size: 40px;
    line-height: 100%;
    vertical-align: top;
}

.circles .circle.small {
    width: 400px;
    height: 400px;
    border-radius: 50%;
    background: #faa077;
    position: absolute;
    z-index: 1;
    top: 80px;
    left: 50%;
    animation: 7s smallmove infinite cubic-bezier(1, 0.22, 0.71, 0.98);
    -webkit-animation: 7s smallmove infinite cubic-bezier(1, 0.22, 0.71, 0.98);
    animation-delay: 1.2s;
    -webkit-animation-delay: 1.2s;
}

.circles .circle.med {
    width: 600px;
    height: 600px;
    border-radius: 50%;
    background: #faa077;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 10%;
    animation: 7s medmove infinite cubic-bezier(0.32, 0.04, 0.15, 0.75);
    -webkit-animation: 7s medmove infinite cubic-bezier(0.32, 0.04, 0.15, 0.75);
    animation-delay: 0.4s;
    -webkit-animation-delay: 0.4s;
}

.circles .circle.big {
    width: 800px;
    height: 800px;
    border-radius: 50%;
    background: #faa077;
    position: absolute;
    z-index: 1;
    top: 200px;
    right: 0;
    animation: 8s bigmove infinite;
    -webkit-animation: 8s bigmove infinite;
    animation-delay: 3s;
    -webkit-animation-delay: 1s;
}

@keyframes smallmove {
    0% {
        top: 10px;
        left: 45%;
        opacity: 1;
    }
    25% {
        top: 300px;
        left: 40%;
        opacity: 0.7;
    }
    50% {
        top: 240px;
        left: 55%;
        opacity: 0.4;
    }
    75% {
        top: 100px;
        left: 40%;
        opacity: 0.6;
    }
    100% {
        top: 10px;
        left: 45%;
        opacity: 1;
    }
}

@keyframes medmove {
    0% {
        top: 0px;
        left: 20%;
        opacity: 1;
    }
    25% {
        top: 300px;
        left: 80%;
        opacity: 0.7;
    }
    50% {
        top: 240px;
        left: 55%;
        opacity: 0.4;
    }
    75% {
        top: 100px;
        left: 40%;
        opacity: 0.6;
    }
    100% {
        top: 0px;
        left: 20%;
        opacity: 1;
    }
}

@keyframes bigmove {
    0% {
        top: 0px;
        right: 4%;
        opacity: 0.5;
    }
    25% {
        top: 100px;
        right: 40%;
        opacity: 0.4;
    }
    50% {
        top: 240px;
        right: 45%;
        opacity: 0.8;
    }
    75% {
        top: 100px;
        right: 35%;
        opacity: 0.6;
    }
    100% {
        top: 0px;
        right: 4%;
        opacity: 0.5;
    }
}
.container {
    width: 100%;
    height: 100%;
    background-color: #e69c48;
}
